@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.help {
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
    color: #666;
    margin: 0 auto;
    text-align: center;
}

.greeting {
    font-weight: 700;
}



.user-item-form {
    width: 60%;
    margin: 15px auto;
    border: 1px solid black;
    padding: 15px;
    border-radius: 6px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}

.form-label {
    width: 150px;
    margin: 0 5px;
    text-align: center;
}

.form-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.table-row {
    margin-top: 20px;
}
.number {
    background-color: #eee;
    border: thin solid #ddd;
    width: 45px;
    height: 45px;
    margin: 10px;
    font-size: 25px;
}
  
.number:focus,
.number:active {
    outline: none;
    border: thin solid #ddd;
}

.star {
    display: inline-block;
    margin: 0 15px;
}
  
.star:after {
    content: "\2605";
    font-size: 45px;
}

.game {
    max-width: 500px;
    margin: 0 auto;
}
  
.body {
    display: -webkit-flex;
    display: flex;
}
  
.left {
    text-align: center;
    width: 50%;
    border: thin solid #ddd;
}
  
.right {
    text-align: center;
    padding: 10px;
    width: 50%;
    border: thin solid #ddd;
}

.timer {
    color: #666;
    margin-top: 3px;
    margin-left: 3px;
}

.create-button {
    display: block;
    margin: 0 auto;
}

.game-done  {
    font-size: 100%;
    font-weight: bold;
    margin-top: 80px;
} 

.user-form {
    margin-top: 20px;
}

.centered {
    margin-top: 200px;
}

.user-form {
    margin: 20px auto;
    max-width: 500px;
}

